@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:700&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2E8B57;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tile {
  display: inline-block;
  background-color: antiquewhite;
  padding: 1rem;
  font-size: 2.6rem;
  border-radius: 5%;
  margin: 0.1rem;
  position: relative;
  height: 1.5rem;
  width: 1.5rem;
  font-family: "Nunito Sans";
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight: 700;
  border: 1px solid black;
  color: green;
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.tile:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.tile__letter {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-60%, -55%);
          transform: translate(-60%, -55%);
}

.tile__letter--double {
  -webkit-transform: translate(-60%, -55%) scaleX(0.65);
          transform: translate(-60%, -55%) scaleX(0.65);
}

.tile__score {
  font-size: 1.2rem;
  position: relative;
  left: 1rem;
  top: -0.3rem;
}

.tile__bonus-name {
  font-size: 0.55rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: black; 
}

.tile__bonus-score {
  font-size: 33%;
  position: absolute;
  right: 0.2rem;
  top: 2.4rem;
  color: black;
}

.App {
  text-align: center;
  max-width: 800px;
  padding: 2rem;
  margin: 2rem auto;
  border: 4px solid white;
  background-color: #3cb371;
}

@media (max-width: 480px) {
  .App {
    border: none;
    margin: 0 auto;
  }

  html {
    background-color: #3cb371;
  }
}

.word-input {
  display: block;
  font-size: 1.2rem;
  padding: 0.2rem;
  margin: 1rem auto;
  border: 1px solid black;
  border-radius: 0.2em;
}

.lang-select {
  font-size: 1rem;
  padding: 0.2rem;
  margin-left: 0.5rem;
  border: 1px solid black;
  border-radius: 0.2em;
  background-color: white;
}

